import Judge from "./judge";
import Query from "./query";
import Report from "./report";

class Util {
    constructor() {
        //
        this.armTypeMap = {
            "": "b",
            "": "b",
            "": "b",
            "": "b",
            "": "b",
            "": "g",
            "": "g",
            "": "g",
            "": "q",
            "": "q",
            "": "q",
            "": "q",
            "": "q"
        };
    }
    //targetArr
    clearBlock(target, targetArr, block, needReport = true, reportLevel = 3) {
        let arr = [];
        console.log("targetArr1", targetArr, target.STATUS);
        let clear = false;
        for (let i in targetArr) {
            if (targetArr[i] != block) arr.push(targetArr[i]);
            else {
                clear = true;
                if (needReport) {
                    let r = new Report();
                    if (targetArr == target.STATUS) r.InitExecuteReport(reportLevel, target, `${block.tactics.name}${block.type}`)
                }
                console.log('');

            }
        }

        if (targetArr == target.STATUS) target.STATUS = arr;
        else {
            targetArr = arr;
        }
        console.log("targetArr2", targetArr, target.STATUS);
        return clear;
    }
    /**
     * 
     * @param {object} target 
     * @param {object} tacticsItem 
     * @param {boolean} isBenefit 
     * @param {number} reportLevel level
     * @returns 
     */
    ClearStatus(target, tacticsItem, isBenefit, reportLevel = 3) {
        let flag = false;
        //
        let arr1 = [];
        for (let i in target.STATUS) {
            let clear = false;
            //
            if (target.STATUS[i].isBenefit == isBenefit) {
                //
                if ((target.STATUS[i].tactics.type == '' || target.STATUS[i].tactics.type == '') && (tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '')) clear = true;
                //
                if (target.STATUS[i].tactics.type == '' && (tacticsItem.type == '' || tacticsItem.type == '')) clear = true;
            }
            if (clear) {
                let r = new Report();
                r.InitExecuteReport(reportLevel, target, `${target.STATUS[i].type}`);
                flag = true;
            }
            else arr1.push(target.STATUS[i]);
        }
        target.STATUS = arr1;
        //
        let arr2 = [];
        for (let i in target.ATTR) {
            let clear = false;
            if (isBenefit == target.ATTR[i].isBenefit) {
                //
                if ((target.ATTR[i].tactics.type == '' || target.ATTR[i].tactics.type == '') && (tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '')) clear = true;
                //
                if (target.ATTR[i].tactics.type == '' && (tacticsItem.type == '' || tacticsItem.type == '')) clear = true;
            }
            if (clear) {
                let report = new Report();
                let text = `${target.ATTR[i].attrType}`
                if (target.ATTR[i].isBenefit) text += ""
                else text += ""
                text += `(${Math.abs(target.ATTR[i].value)})`
                report.InitExecuteReport(reportLevel, target, text);
                flag = true;
            }
            else arr2.push(target.ATTR[i]);
        }
        target.ATTR = arr2;
        //
        let arr3 = [];
        for (let i in target.DAMAGE_VAR) {
            let clear = false;
            if (target.DAMAGE_VAR[i].isBenefit == isBenefit) {
                //
                if ((target.DAMAGE_VAR[i].tactics.type == '' || target.DAMAGE_VAR[i].tactics.type == '') && (tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '')) {
                    clear = true;
                }
                //
                if (target.DAMAGE_VAR[i].tactics.type == '' && (tacticsItem.type == '' || tacticsItem.type == '')) clear = true;
            }
            if (clear) {
                let report = new Report();
                let str1 = target.DAMAGE_VAR[i].isMakeDamage ? "" : "";
                let str2 = "";
                if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                else if (target.DAMAGE_VAR[i].damageType == '') str2 = "";
                let str3 = "";
                if (target.DAMAGE_VAR[i].value < -666) str3 = "";
                else if (target.DAMAGE_VAR[i].value > 0) str3 = "";
                else str3 = "";
                let str4 = ''
                if (target.DAMAGE_VAR[i].value >= -666) str4 = `${Math.abs(target.DAMAGE_VAR[i].value)}%`
                report.InitExecuteReport(reportLevel, target, `${target.DAMAGE_VAR[i].tactics.name}${str1 + str2 + str3 + str4}`);
                flag = true;
            }
            else arr3.push(target.DAMAGE_VAR[i]);
        }
        target.DAMAGE_VAR = arr3;
        //
        let arr4 = [];
        for (let i in target.ODD_VAR) {
            let clear = false;
            if (target.ODD_VAR[i].isBenefit == isBenefit) {
                //
                if ((target.ODD_VAR[i].tactics.type == '' || target.ODD_VAR[i].tactics.type == '') && (tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '')) {
                    clear = true;
                }
                //
                if (target.ODD_VAR[i].tactics.type == '' && (tacticsItem.type == '' || tacticsItem.type == '')) clear = true;
            }
            if (clear) {
                if (target.ODD_VAR[i].value != 0) {
                    let r = new Report();
                    let txt = `${attrType.ODD_VAR[i].workTactics.name}${target.ODD_VAR[i].value > 0 ? "" : ""}${Math.abs(target.ODD_VAR[i].value)}%`;
                    r.InitExecuteReport(reportLevel, target, txt);
                }
                if (target.ODD_VAR[i].skipTime > 0) {
                    let r = new Report();
                    let txt = `${attrType.ODD_VAR[i].workTactics.name}${target.ODD_VAR[i].skipTime}`;
                    r.InitExecuteReport(reportLevel, target, txt);
                }
                flag = true;
            }
            else arr4.push(target.ODD_VAR[i]);
        }
        //
        let arr5 = [];
        for (let i in target.RANGE) {
            let clear = false;
            if (target.RANGE[i].isBenefit == isBenefit) {
                //
                if ((target.RANGE[i].tactics.type == '' || target.RANGE[i].tactics.type == '') && (tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '')) {
                    clear = true;
                }
                //
                if (target.RANGE[i].tactics.type == '' && (tacticsItem.type == '' || tacticsItem.type == '')) clear = true;
            }
            if (clear) {
                let text = "";
                if (this.isCommonAttack) text = "";
                else text = "";
                if (target.RANGE[i].value > 0) {
                    text += `${target.RANGE[i].value}`;
                }
                else {
                    text += `${Math.abs(target.RANGE[i].value)}`;
                }
                text += "";
                let r = new Report();
                r.InitExecuteReport(reportLevel, target, text);
                flag = true;
            }
            else arr5.push(target.RANGE[i]);
        }
        target.RANGE = arr5;
        //
        let arr6 = [];
        for(let i in target.STATUS_DAMAGE_VAR){
            let clear = false;
            if(target.STATUS_DAMAGE_VAR[i].isBenefit == isBenefit){
                if((target.STATUS_DAMAGE_VAR[i].tactics.type == "" || target.STATUS_DAMAGE_VAR[i].tactics.type == "") && (tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '')){
                    clear = true;
                }
                //
                if (target.STATUS_DAMAGE_VAR[i].tactics.type == '' && (tacticsItem.type == '' || tacticsItem.type == '')) clear = true;
            }
            if (clear) {
                let r = new Report();
                r.InitExecuteReport(reportLevel, target, `${target.STATUS_DAMAGE_VAR[i].statusType}${target.STATUS_DAMAGE_VAR[i].value > 0 ? "":""}${Math.abs(target.STATUS_DAMAGE_VAR[i].value)}`);
                flag = true;
            }
            else arr6.push(target.STATUS_DAMAGE_VAR[i]);
        }
        target.STATUS_DAMAGE_VAR = arr6
        //
        let arr = [];
        if(!isBenefit){
            for (let i in target.SPECIAL_STATUS) {
                let clear = false;
                //
                if ((target.SPECIAL_STATUS[i].tactics.type == '' || target.SPECIAL_STATUS[i].tactics.type == '') && (tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '' || tacticsItem.type == '')) {
                    clear = true;
                }
                //
                if (target.SPECIAL_STATUS[i].tactics.type == '' && (tacticsItem.type == '' || tacticsItem.type == '')) clear = true;
                if (clear) {
                    let r = new Report();
                    let txt = `${target.SPECIAL_STATUS[i].type}`;
                    r.InitExecuteReport(reportLevel, target, txt);
                    flag = true;
                }
                else {
                    arr.push(target.SPECIAL_STATUS[i]);
                }
            }
        }
        target.SPECIAL_STATUS = arr;
        return flag;
    }
    //
    TransferArms(armStr) {
        return this.armTypeMap[armStr];
    }
    //
    TransforArmForDamage(armStr){
        let res = this.armTypeMap[armStr];
        if(armStr == "") res = "g";
        return res;
    }
    //
    GetExecutor(allTeam) {
        let q = new Query();
        let judge = new Judge()
        let res = null;
        let fast = -1000;
        //
        for (let i in allTeam) {
            for (let j in allTeam[i]) {
                if (allTeam[i][j].action && allTeam[i][j].alive) {
                    let speed = q.GetAttr(allTeam[i][j], "");
                    if (judge.IsGetStatus(allTeam[i][j], "")) speed += 1000;
                    if (speed > fast) {
                        res = allTeam[i][j];
                        fast = speed;
                    }
                    else if (speed == fast) {
                        let rand = Math.floor(Math.random() * 2);
                        if (rand == 1) {
                            res = allTeam[i][j];
                            fast = speed;
                        }
                    }
                }
            }
        }
        return res;
    }
}

export default Util;